<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuLogin/>
            <SidemenuListCustomer action="2"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>1:1 </h2>
                </div>
                <div class="csInfo">
                    <p>*    .</p>
                    <p>*       .</p>
                </div>
                <div class="writingWrap">
                    <table>
                        <tr>
                            <th></th>
                            <td></td>
                        </tr>
                        <tr class="title">
                            <th></th>
                            <td> </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td></td>
                        </tr>
                        <tr class="content">
                            <th></th>
                            <td> </td>
                        </tr>
                        <tr class="answer">
                            <th></th>
                            <td>  .</td>
                        </tr>
                    </table>
                    <div class="buttonBox">
                        <button type="button" onclick="location.href='/#/Inquiries'"></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListCustomer from '../SidemenuListCustomer'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'InquiriesTexting',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuListCustomer,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    box-sizing: border-box;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    width: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    padding: 10px 20px;
    margin: 10px 0px 20px;
}
.writingWrap {
    border-top: 3px solid #e3e3e3;
}
.writingWrap > table{
    line-height: 2.5;
    border-collapse: collapse;
    width: 100%;
}
.writingWrap > table > tr > th {
    width: 170px;
    border-right: 1px solid #e3e3e3;
    border-bottom: 1px solid #e3e3e3;
}
.writingWrap > table > tr > td {
    padding-left: 15px;
    border-bottom: 1px solid #e3e3e3;
}
.content > th {
    height: 200px;
}
.answer > td {
    height: 200px;
    text-align: center;
}
.buttonBox {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
.buttonBox > button {
    width: 100px;
    height: 40px;
    margin-left: 10px;
}
</style>



