<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuLogin/>
            <SidemenuSpam action="3"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2> </h2>
                </div>
                <div class="csInfo">
                    <p>                        .</p>
                    &nbsp;
                    <p> 502(    )</p>
                    <p>                     .</p>
                    <p> 1       .</p>
                    <p> 1  2             .</p>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuSpam from '../SidemenuSpam'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'Email',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuSpam,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    margin: 40px 0;
    line-height: 1.6;
    padding: 20px;
    font-size: 14px;
}
.csInfo > h3 {
    margin-top: 20px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
}
.csInfo > h4 {
    margin-top: 20px;
    padding-bottom: 10px;
}
</style>



