<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuAfterLogin/>
            <!--<SidemenuLogin/>-->
            <SidemenuListMyInfo action="1"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2> </h2>
                </div>
                <div class="csInfo">
                    <p>*     .</p>
                    <p>*       .</p>
                    <p>*        .</p>
                    <p>*, ,            .</p>
                </div>
                <div class="writingWrap">
                    <table>
                        <tr class="pass">
                            <th> </th>
                            <td><input type="password"></td>
                        </tr>
                        <tr class="pass">
                            <th> </th>
                            <td><input type="password"><br>8~16 ,,(~!@#$%^&*?  ) </td>
                        </tr>
                        <tr class="pass">
                            <th>  </th>
                            <td><input type="password"><br>    .</td>
                        </tr>
                    </table>
                    <div class="buttonBox">
                        <button></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListMyInfo from '../SidemenuListMyInfo'
import SidemenuHelp from '../SidemenuHelp'
import SidemenuAfterLogin from '../SidemenuAfterLogin'

export default {
  name: 'MyPassword',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuAfterLogin,
    SidemenuListMyInfo,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    box-sizing: border-box;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csInfo {
    width: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    padding: 10px 20px;
    margin: 10px 0px 20px;
}
.writingWrap > table {
    width: 100%;
    border-collapse: collapse;
    line-height: 3;
}
.pass > td > input {
    width: 300px;
    height: 25px;
}
.writingWrap > table > tr > th {
    width: 200px;
    border:1px solid #e3e3e3;
    background-color: #f6f5ff;
    font-size: 15px;
}
.writingWrap > table > tr > td {
    border:1px solid #e3e3e3;
    padding-left: 15px;
}
.buttonBox {
    margin-top: 20px;
    text-align: center;
}
.buttonBox > button {
    width: 150px;
    height: 50px;
}
</style>



