<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuLogin/>
            <SidemenuListCustomer action="1"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2>(FAQ)</h2>
                </div>
                <div class="csList">
                </div>
                <Accordion v-for="(q, index) in list" :key="index" :item="list[index]"/>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListCustomer from '../SidemenuListCustomer'
import SidemenuHelp from '../SidemenuHelp'
import Accordion from '../Accordion'

export default {
  name: 'Faq',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuListCustomer,
    SidemenuHelp,
    Accordion
  },
  data () {
    return {
      list: [
        {
          title: '   ?',
          info: '  .             .'
        },
        {
          title: '         ',
          info: '         .'
        },
        {
          title: '   ?',
          info: '  .             .'
        },
        {
          title: '   ?',
          info: '  .             .'
        },
        {
          title: '   ?',
          info: '  .             .'
        },
        {
          title: '   ?',
          info: '  .             .'
        },
        {
          title: '   ?',
          info: '  .             .'
        },
        {
          title: '   ?',
          info: '  .             .'
        },
        {
          title: '   ?',
          info: '  .             .'
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    padding-bottom: 7px;
    margin-top: 10px;
}
.csList {
    margin-top: 20px;
    text-align: center;
    line-height: 2;
}
</style>



