                        <tr v-for="(q, index) in list" :key="index">
                            <td>{{ q.number }}</td>
                            <td><a href="/#/CustomerTexting">[]{{ q.title }}</a></td>
                            <td>{{ q.day }}</td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListCustomer from '../SidemenuListCustomer'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'CustomerService',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuListCustomer,
    SidemenuHelp
  },
  data () {
    return {
      list: [
        {
          number: '',
          title: '',
          day: '2016-06-10'
        },
        {
          number: '',
          title: '',
          day: '2016-06-10'
        },
        {
          number: '',
          title: '',
          day: '2016-06-10'
        },
        {
          number: '',
          title: '',
          day: '2016-06-10'
        },
        {
          number: '',
          title: '',
          day: '2016-06-10'
        },
        {
          number: '',
          title: '',
          day: '2016-06-10'
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  outline: none;
  list-style: none;
}
.csWrap {
  width: 100%;
}
.csContainer {
  width: 820px;
}
.csHeader {
  border-bottom: 2px solid #ccc;
  box-sizing: border-box;
  padding-bottom: 7px;
  margin-top: 10px;
}
.csInfo {
  border: 1px solid #ccc;
  box-sizing: border-box;
  padding: 10px 20px;
  margin: 10px 0px 20px;
  line-height: 2;
}
.csList {
  text-align: center;
  line-height: 2;
}
.csList > table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 30px;
}
.csList > table > tr > th {
  border: 1px solid #ccc;
}
.csList > table > tr > td {
  border: 1px solid #ccc;
}
.csList > table > tr > th:nth-child(1) {
  width: 80px;
}
.csList > table > tr > th:nth-child(3) {
  width: 120px;
}
</style>



