<template>
<div>
    <div class="chargingContent">
        <table>
            <tr>
                <th></th>
                <th></th>
                <th>(SMS)</th>
                <th>(LMS)</th>
                <th>(MMS)</th>
            </tr>
            <tr v-for="(d, index) in listData" :key="index">
                <td><input type="radio"></td>
                <td>{{ d.pay }}</td>
                <td>{{ d.sms }}</td>
                <td>{{ d.lms }}</td>
                <td>{{ d.mms }}</td>
            </tr>
        </table>
    </div>
</div>
</template>

<script>
export default {
  name: 'Virtual',
  data () {
    return {
      listData: [
        {
          pay: '10,000',
          sms: '697',
          lms: '259',
          mms: '70'
        },
        {
          pay: '10,000',
          sms: '697',
          lms: '259',
          mms: '70'
        },
        {
          pay: '10,000',
          sms: '697',
          lms: '259',
          mms: '70'
        },
        {
          pay: '10,000',
          sms: '697',
          lms: '259',
          mms: '70'
        },
        {
          pay: '10,000',
          sms: '697',
          lms: '259',
          mms: '70'
        }
      ]
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.chargingContent > table {
  width: 100%;
  text-align: center;
  line-height: 2;
  border: 1px solid #000;
  border-collapse: collapse;
  margin: 20px 0;
}
.chargingContent > table > tr > th {
  border: 1px solid #ccc;
  color:orchid;
}
.chargingContent > table > tr > td {
  border: 1px solid #ccc;
}
</style>



