<template>
<div>
    <div class="chargingContent">
        <div>
            <button></button>
            <button></button>
            <button></button>
        </div>
        <table>
            <tr>
                <th><input type="radio"></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr v-for="(d, index) in listData" :key="index">
                <td><input type="radio"></td>
                <td>{{ d.number }}</td>
                <td>{{ d.day }}</td>
                <td>{{ d.lms }}</td>
                <td>{{ d.mms }}</td>
                <td>{{ d.lms }}</td>
                <td><a href="#"></a></td>
            </tr>
        </table>
    </div>
</div>
</template>

<script>
export default {
  name: 'PatmentVirtual',
  data () {
    return {
      listData: [
        {
          number: '1',
          day: '2021-06-10',
          lms: '',
          mms: '7,000,000'
        }
      ]
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.chargingContent > table {
  width: 100%;
  text-align: center;
  line-height: 2;
  border: 1px solid #000;
  border-collapse: collapse;
  margin: 20px 0;
}
.chargingContent > table > tr > th {
  border: 1px solid #ccc;
  color:orchid;
}
.chargingContent > table > tr > td {
  border: 1px solid #ccc;
}
</style>



