<template>
  <div>
    <HeaderTop/>
    <Header/>
    <div class="csWrap">
        <div class="componentSection">
          <div>
            <SidemenuLogin/>
            <SidemenuListCustomer action="0"/>
            <SidemenuHelp/>
          </div>
            <div class="csContainer">
                <div class="csHeader">
                    <h2></h2>
                </div>
                <div class="writingWrap">
                    <table>
                        <tr class="title">
                            <th></th>
                            <td></td>
                        </tr>
                        <tr class="content">
                            <th></th>
                            <td> ,  .
                                 .
                            DB    
                                .
                            -  : 2021.6.14() 19:00 ~21:00 (2)
                            -   :         
                            (   )
                              ,       .
                            .</td>
                        </tr>
                        <tr>
                            <th></th>
                            <td>2021-06-10</td>
                        </tr>
                    </table>
                    <div class="buttonBox">
                        <button type="button" onclick="location.href='/#/CustomerService'"></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '../Header'
import HeaderTop from '../HeaderTop'
import Footer from '../Footer'
import SidemenuLogin from '../SidemenuLogin'
import SidemenuListCustomer from '../SidemenuListCustomer'
import SidemenuHelp from '../SidemenuHelp'

export default {
  name: 'CustomerTexting',
  components: {
    Header,
    Footer,
    HeaderTop,
    SidemenuLogin,
    SidemenuListCustomer,
    SidemenuHelp
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
    outline: none;
    list-style: none;
}
.csWrap {
    width: 100%;
}
.csContainer {
    width: 820px;
}
.csHeader {
    border-bottom: 2px solid #ccc;
    padding-bottom: 7px;
    margin-top: 10px;
}
.writingWrap {
    border-top: 2px solid #e3e3e3;
    margin-top: 40px;
}
.writingWrap > table{
    line-height: 2.5;
    border-collapse: collapse;
    width: 100%;
}
.writingWrap > table > tr > th {
    width: 170px;
    border-right: 1px solid #e3e3e3;
    border-bottom: 1px solid #e3e3e3;
}
.writingWrap > table > tr > td {
    padding-left: 15px;
    border-bottom: 1px solid #e3e3e3;
}
.content > th {
    height: 400px;
}
.buttonBox {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
.buttonBox > button {
    width: 100px;
    height: 40px;
    margin-left: 10px;
}
</style>



